#graphContainer {
    float:left;
    z-index: 1;
    overflow-x:hidden;
    overflow-y:visible;
    height: 800px;
    width: 1800px;
    background: url('../img/grid.gif');
}

#graphuibody {
    height: 800px;
}

#filter_table > thead {
    text-align: left;
}

.filter_name {
    text-align: center;
    font-size: 20px;
}

.filter_args {
    text-align: center;
    font-size: 14px;
}

.filter_status {
    color:blueviolet;
    font-weight: bold;
    font-size: 12px;
    white-space: break-spaces;
}

.filter_bytes {
    font-style: italic;
    font-size: 10px;
}

.ipid_buffer {
    font-size: 10px;
    font-family: 'Courier New', Courier, monospace;
}

.details_pane {
    min-width: 350px;
    padding-left:25px;
    /* float: right; */
    font-size: 16px;
    font-family: 'Courier New', Courier, monospace;
    border-left: 2px blueviolet dashed;
    overflow-x: auto;
    overflow-y: auto;
    height:100%;
    display: block;

}



.details_header {
    font-family:Arial, Helvetica, sans-serif;
    font-size: 14px;
    /* text-align: center; */
    font-weight: bolder;
    color:cornflowerblue;
    margin: 10px;
    z-index: 30;
    /* position: absolute; */
}

.pidname_td {
    text-align: center;
    font-weight: bold;
    padding: 2px;
    background-color: lightgray;
}

.argsbtn {
    text-decoration: none;
}

.argsbtn:hover {
    font-weight: bolder;
}